/*global kakao*/

/**
 *     
 * @param {Object} mapInstance - Kakao  
 * @param {Object} position -   (kakao.maps.LatLng)
 * @param {number} radius -   ()
 * @returns {Promise<Object[]>} -      
 */
export async function searchSubwayStations(mapInstance, position, radius = 1500) {
    return new Promise((resolve, reject) => {
        if (!mapInstance || !position) {
            reject(new Error("      ."));
            return;
        }

        const ps = new kakao.maps.services.Places(mapInstance);

        ps.categorySearch(
            'SW8', //   
            (data, status) => {
                if (status === kakao.maps.services.Status.OK) {
                    const results = data.map((place) => {
                        //    
                        const imageSrc = '/subway-icon.png'; //     
                        const imageSize = new kakao.maps.Size(18, 18); //   (x)
                        const imageOption = { offset: new kakao.maps.Point(12, 12) }; //  
                        const markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption);

                        //   
                        const marker = new kakao.maps.Marker({
                            map: mapInstance,
                            position: new kakao.maps.LatLng(place.y, place.x),
                            image: markerImage,
                        });

                        // InfoWindow 
                        const infowindow = new kakao.maps.InfoWindow({
                            content: `<div style="padding:5px;font-size:12px;">${place.place_name}</div>`,
                            zIndex: 1,
                        });

                        //   
                        kakao.maps.event.addListener(marker, 'mouseover', () => {
                            infowindow.open(mapInstance, marker); //   InfoWindow 
                        });

                        //   
                        kakao.maps.event.addListener(marker, 'mouseout', () => {
                            infowindow.close(); //   InfoWindow 
                        });

                        return { name: place.place_name, marker };
                    });

                    resolve(results); //       
                } else {
                    reject(new Error("     ."));
                }
            },
            {
                location: position,
                radius,
            }
        );
    });
}
