/* global kakao */


/**
 *  
 * @param {string} containerId -   HTML  ID
 * @returns {Object} - Kakao  
 */


let map;
let markers = [];

export function initializeMap(containerId) {
    const mapContainer = document.getElementById(containerId); //   div
    const mapOption = {
        center: new kakao.maps.LatLng(37.537187, 127.005476), //  
        level: 7 //   
    };

    //   
    map = new kakao.maps.Map(mapContainer, mapOption);
    console.log("  ", map);

    return map;
}


/**
 *   
 * @param {Object} map - Kakao  
 * @param {string} address - 
 * @param {string} memberName -  
 * @returns {Object} -   
 */

export function addMarker(map, address, memberName) {
    const geocoder = new kakao.maps.services.Geocoder();

    return new Promise((resolve, reject) => {
        geocoder.addressSearch(address, (result, status) => {
            if (status === kakao.maps.services.Status.OK) {
                const coords = new kakao.maps.LatLng(result[0].y, result[0].x);

                const marker = new kakao.maps.Marker({
                    position: coords,
                    map: map,
                });

                const infoWindow = new kakao.maps.InfoWindow({
                    content: `<div style="padding:5px;">${memberName}</div>`,
                    removable: true,
                });

                kakao.maps.event.addListener(marker, 'mouseover', () => {
                    infoWindow.open(map, marker);
                });
                kakao.maps.event.addListener(marker, 'mouseout', () => {
                    infoWindow.close();
                });

                const markerObj = { address, marker };
                markers.push(markerObj); //   
                resolve(markerObj);
            } else {
                reject(new Error(`  : ${address},  : ${status}`));
            }
        });
    });
}





export function removeMarker(address) {
    const markerObj = markers.find((m) => m.address === address);

    if (markerObj && markerObj.marker instanceof kakao.maps.Marker) {
        markerObj.marker.setMap(null); //   
        markers = markers.filter((m) => m.address !== address); //   
        console.log("  :", address);
        return true;
    } else {
        console.warn("        :", markerObj);
        return false;
    }
}



export function clearMarkers() {
    markers.forEach((markerObj) => {
        if (markerObj.marker instanceof kakao.maps.Marker) {
            markerObj.marker.setMap(null); //   
        }
    });
    markers = []; //   
    console.log("  .");
}
