/* global kakao */

/**
 *       
 * @param {Array} members -   
 * @param {Object} geocoder -   Geocoder 
 * @returns {Promise<Object>} -   { x, y }
 */

//    
export async function calculateMeanCoordinates(members, geocoder) {
    if (members.length === 0) {
        return null;
    }

    const addressPromises = members.map(
        (member) =>
            new Promise((resolve, reject) => {
                geocoder.addressSearch(member.address, (result, status) => {
                    if (status === kakao.maps.services.Status.OK) {
                        resolve({ x: parseFloat(result[0].x), y: parseFloat(result[0].y) });
                    } else {
                        reject(new Error(`  : ${member.address}`));
                    }
                });
            })
    );

    try {
        const coordinates = await Promise.all(addressPromises);

        const sum = coordinates.reduce(
            (acc, coord) => ({
                x: acc.x + coord.x,
                y: acc.y + coord.y,
            }),
            { x: 0, y: 0 }
        );

        return {
            x: sum.x / coordinates.length,
            y: sum.y / coordinates.length,
        };
    } catch (error) {
        console.error("  :", error);
        throw error;
    }

}


/**
 *    
 * @param {Object} map -   
 * @param {Object} meanCoord -   { x, y }
 * @returns {Object} -  
 */

//     

let markerCounter = 0;

export function addMeanMarker(map, meanCoord) {
    if (!meanCoord) {
        console.error("   .");
        return null;
    }

    const meanLatLng = new kakao.maps.LatLng(meanCoord.y, meanCoord.x);

    //  
    const meanMarker = new kakao.maps.Marker({
        position: meanLatLng,
        map: map,
    });

    // ID 
    const markerId = `mean-marker-${++markerCounter}`;
    console.log(`   , ID: ${markerId}`);

    map.setCenter(meanLatLng);
    return { marker: meanMarker, id: markerId };
}





/**
 *    
 * @param {Object} map -   
 * @param {Object} meanCoord -   { x, y }
 * @param {number} radius -  ( )
 * @returns {Object} -  
 */

export function addCircle(map, meanCoord, radius = 1500) {
    if (!meanCoord) {
        console.error("   .");
        return null;
    }

    const meanLatLng = new kakao.maps.LatLng(meanCoord.y, meanCoord.x);

    //   
    const circle = new kakao.maps.Circle({
        center: meanLatLng,
        radius: radius,
        strokeWeight: 5,
        strokeColor: '#75B8FA',
        strokeOpacity: 1,
        strokeStyle: 'dashed',
        fillColor: '#CFE7FF',
        fillOpacity: 0.7,
        map: map,
    });

    console.log(` ${radius}m  .`);
    return circle;
}


