import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom'; // useNavigate 
import './profile.css';

const Profile = () => {
  const [selectedBlocks, setSelectedBlocks] = useState([]);
  const [isMouseDown, setIsMouseDown] = useState(false);
  const navigate = useNavigate(); // useNavigate  

  const toggleBlockSelection = (day, hour) => {
    if (isSelected(day, hour)) {
      setSelectedBlocks((prevBlocks) =>
        prevBlocks.filter((block) => !(block.day === day && block.hour === hour))
      );
    } else {
      setSelectedBlocks((prevBlocks) => [...prevBlocks, { day, hour }]);
    }
  };

  const handleMouseDown = (day, hour) => {
    setIsMouseDown(true);
    toggleBlockSelection(day, hour);
  };

  const handleMouseEnter = (day, hour) => {
    if (isMouseDown) {
      toggleBlockSelection(day, hour);
    }
  };

  const handleMouseUp = () => {
    setIsMouseDown(false);
  };

  const isSelected = (day, hour) => {
    return selectedBlocks.some((block) => block.day === day && block.hour === hour);
  };

  // main   
  const goToMainPage = () => {
    navigate('/'); //    (main )
  };

  return (
    <div className="profile-page">
      <div className="profile-section">
        <div className="profile-picture"></div>
        <button className="edit-profile-btn"> </button>
        <div className="profile-info">
          <input type="text" placeholder="" className="profile-input" />
          <input type="email" placeholder="" className="profile-input" />
          <input type="text" placeholder="" className="profile-input" />
          <input type="tel" placeholder="" className="profile-input" />
        </div>
        <button className="edit-info-btn"> </button>
      </div>
      <div className="timetable-section">
        <table className="timetable" onMouseLeave={handleMouseUp}>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {Array.from({ length: 24 }).map((_, hour) => (
              <tr key={hour}>
                <td>{`${String(hour).padStart(2, '0')}:00`}</td>
                {Array.from({ length: 7 }).map((_, day) => (
                  <td
                    key={day}
                    className={`timetable-block ${isSelected(day, hour) ? 'selected' : ''}`}
                    onMouseDown={() => handleMouseDown(day, hour)}
                    onMouseEnter={() => handleMouseEnter(day, hour)}
                    onMouseUp={handleMouseUp}
                  ></td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
        <button className="edit-timetable-btn" onClick={goToMainPage}></button> {/*    main   */}
      </div>
    </div>
  );
};

export default Profile;
