import React, { useEffect, useState } from "react";
import "./team.css";
import { Link } from 'react-router-dom';
import { useNavigate } from "react-router-dom";
import axios from "axios";
import MyCalendar from '../MyCalendar/MyCalendar';
import { io } from 'socket.io-client';

const socket = io('http://localhost:5000');



function Team({ user, logout }) {
    const [isModalOpen, setModalOpen] = useState(false);
    const [teamName, setTeamName] = useState("");
    const [error, setError] = useState("");

    const [teams, setTeams] = useState([]);
    const [selectedTeamId, setSelectedTeamId] = useState(null);
    const [selectedTeamName, setSelectedTeamName] = useState(" ");
    const [selectedTeamColor, setSelectedTeamColor] = useState("#cecece");

    const [teamMembers, setTeamMembers] = useState([]);
    const [activeTab, setActiveTab] = useState("");

    const [isJoinMode, setJoinMode] = useState(false);
    const [inviteCode, setInviteCode] = useState("");

    const [filteredEvents, setFilteredEvents] = useState([]);

    const [chatMessages, setChatMessages] = useState([]);
    const [newMessage, setNewMessage] = useState("");

    const navigate = useNavigate();

    useEffect(() => {
        socket.on('connect', () => {
            console.log("WebSocket :", socket.id);
        });

        socket.on('disconnect', () => {
            console.log("WebSocket  .");
        });

        return () => {
            socket.off('connect');
            socket.off('disconnect');
        };
    }, []);


    useEffect(() => {
        const fetchMessages = async () => {
            try {
                if (!selectedTeamId) {
                    console.warn('  ID .');
                    setChatMessages([]);
                    return;
                }

                console.log(' :', `http://localhost:5000/api/chat/${selectedTeamId}`);

                const response = await axios.get(`http://localhost:5000/api/chat/${selectedTeamId}`);
                const messages = response.data;

                console.log('  :', messages);
                setChatMessages(messages);
            } catch (error) {
                console.error('   :', error);
            }
        };

        fetchMessages();
    }, [selectedTeamId]);



    useEffect(() => {
        const handleNewMessage = (message) => {
            console.log("  :", message);

            //    
            setChatMessages((prevMessages) => {
                const isDuplicate = prevMessages.some((msg) => msg._id === message._id);
                if (isDuplicate) return prevMessages;
                return [...prevMessages, message];
            });
        };

        socket.on('newMessage', handleNewMessage);

        return () => {
            socket.off('newMessage', handleNewMessage);
        };
    }, []);



    const sendMessage = () => {
        if (newMessage.trim()) {
            const message = {
                _id: Date.now(), //  ID
                teamId: selectedTeamId,
                userId: user._id,
                userName: user.name, //   (  )
                message: newMessage,
                timestamp: new Date(), //  
                isTemporary: true, //   
            };

            //   
            setChatMessages((prevMessages) => [...prevMessages, message]);

            //   
            socket.emit('chatMessage', message);

            //   
            setNewMessage("");
        }
    };













    useEffect(() => {
        const fetchFilteredEvents = async () => {
            if (!user) return;

            try {
                const response = await axios.get("http://localhost:5000/api/filtered-events", {
                    params: {
                        userId: user._id,
                        view: selectedTeamId ? "team" : "personal", // view  
                        teamId: selectedTeamId || null,            // teamId 
                    },
                });
                setFilteredEvents(response.data);
            } catch (err) {
                console.error("   :", err);
                setError("    .");
            }
        };

        fetchFilteredEvents();
    }, [user, selectedTeamId]);




    const openModal = () => setModalOpen(true);
    const closeModal = () => {
        setModalOpen(false);
        setTeamName("");
        setError("");
    };

    const handleLogout = () => {
        logout();
        navigate('/');
    };

    const handleCreateTeam = async () => {
        if (!teamName.trim()) {
            setError("  .");
            return;
        }
        try {
            const response = await axios.post("http://localhost:5000/api/teams", {
                name: teamName,
                creatorId: user._id,
            });
            alert(`  : ${response.data.name}`);
            setTeams([...teams, response.data]);
            closeModal();
        } catch (error) {
            console.error("  :", error);
            setError("  .");
        }
    };

    const handleTeamClick = async (teamId) => {
        setSelectedTeamId(teamId);

        const team = teams.find(t => t._id === teamId);
        setSelectedTeamName(team ? team.name : " ");

        if (teamId) {
            const teamIndex = teams.findIndex(t => t._id === teamId);
            const color = getButtonColor(teamIndex);
            setSelectedTeamColor(color);

            document.body.style.backgroundColor = backgroundColors[color] || "#f2f2f2";

            try {
                const response = await axios.get(`http://localhost:5000/api/teams/${teamId}/members`);
                setTeamMembers(response.data);
            } catch (error) {
                console.error("   :", error);
                setTeamMembers([]);
            }
        } else {
            setSelectedTeamColor("#cecece");
            document.body.style.backgroundColor = "#f2f2f2";
            setTeamMembers([]);
        }
    };

    const baseColors = ["#D35940", "#9ACD4C", "#63A0CC"];
    const backgroundColors = {
        "#D35940": "#FFE1E5",
        "#9ACD4C": "#EAF4DB",
        "#63A0CC": "#C0D9EA",
    };

    const getButtonColor = (index) => baseColors[index % baseColors.length];

    const generateInviteCode = async (teamId) => {
        try {
            const response = await axios.post(`http://localhost:5000/api/teams/${teamId}/invite-code`);
            alert(` : ${response.data.inviteCode}`);
        } catch (error) {
            console.error('   :', error);
            alert('   .');
        }
    };

    const joinTeamWithCode = async (code, userId) => {
        try {
            const response = await axios.post('http://localhost:5000/api/teams/join', {
                inviteCode: code,
                userId,
            });
            alert('  !');
            console.log('  ID: ', response.data.teamId);
        } catch (error) {
            console.error('  :', error);
            alert('  .');
        }
    };



    useEffect(() => {
        const fetchTeams = async () => {
            if (!user) return;

            try {
                const response = await axios.get("http://localhost:5000/api/teams", {
                    params: { userId: user._id },
                });
                setTeams(response.data);
            } catch (error) {
                console.error("   :", error);
            }
        };
        fetchTeams();
    }, [user]);



    return (
        <div className="team-container">

            <aside className="team-header">
                <Link to="/team" className="logo-link">
                    <img src="/imo_logo_small.png" alt="IMO " className="logo" />
                </Link>
                <div className="profile-section">
                    <img src="/profile.png" alt="" className="profile-icon" />
                    <div className="user-info">
                        <div className="user-name">{user ? user.name : ''}</div>
                        <div className="edit-info">
                            <button className="my-info"> </button>
                            <button className="logout" onClick={handleLogout}></button>
                        </div>
                    </div>
                </div>
            </aside>

            <main className="main-content">
                <div className="calendar">
                    <div className="team-buttons">
                        <button
                            className={`team-button ${selectedTeamId === null ? 'active' : ''}`}
                            onClick={() => handleTeamClick(null)}
                        >
                            0
                        </button>

                        {teams.map((team, index) => (
                            <button
                                key={team._id}
                                className={`team-button ${selectedTeamId === team._id ? 'active' : ''}`}
                                onClick={() => handleTeamClick(team._id)}
                                style={{ backgroundColor: getButtonColor(index) }}
                            >
                                {index + 1}
                            </button>
                        ))}
                        <button className="team-button" onClick={openModal}>+</button>
                    </div>

                    <div className="mycalendar">
                        <MyCalendar user={user} teamId={selectedTeamId} teamColor={selectedTeamColor} />
                    </div>
                </div>

                <aside className="notifications">
                    <div className="tab-content">
                        {/*    */}
                        <h2 className="board-title">
                            {selectedTeamId ? "" : ""}
                        </h2>

                        {activeTab === "" && (
                            <div className="board">
                                {selectedTeamId ? (
                                    //  UI 
                                    <>
                                        {/* ...    */}
                                    </>
                                ) : (
                                    //   UI 
                                    error ? (
                                        <p>{error}</p>
                                    ) : filteredEvents.length > 0 ? (
                                        <ul>
                                            {filteredEvents
                                                .flatMap((event) => {
                                                    if (event.isRecurring) {
                                                        const recurrenceEvents = [];
                                                        const startDate = new Date(event.start);
                                                        for (let i = 0; i < 10; i++) {
                                                            const recurringStart = new Date(startDate);
                                                            recurringStart.setDate(startDate.getDate() + i * 7);
                                                            recurrenceEvents.push({
                                                                ...event,
                                                                start: recurringStart,
                                                            });
                                                        }
                                                        return recurrenceEvents;
                                                    }
                                                    return [event];
                                                })
                                                .sort((a, b) => new Date(a.start) - new Date(b.start)) //   
                                                .map((event) => {
                                                    const teamColor = event.teamId
                                                        ? (() => {
                                                            const teamIndex = teams.findIndex(
                                                                (team) => team._id === event.teamId
                                                            );
                                                            return teamIndex !== -1 ? getButtonColor(teamIndex) : "#cecece";
                                                        })()
                                                        : "#cecece";

                                                    return (
                                                        <li
                                                            key={`${event._id}-${event.start}`}
                                                            style={{
                                                                backgroundColor: teamColor,
                                                                padding: "10px",
                                                                borderRadius: "8px",
                                                                marginBottom: "8px",
                                                                listStyle: "none",
                                                            }}
                                                        >
                                                            <strong>{event.title}</strong> :{" "}
                                                            {new Date(event.start).toLocaleString("ko-KR", {
                                                                month: "long",
                                                                day: "numeric",
                                                                hour: "numeric",
                                                                minute: "numeric",
                                                                hour12: true,
                                                            })}
                                                            {event.place && (
                                                                <div
                                                                    style={{
                                                                        marginTop: "4px",
                                                                        fontSize: "0.9em",
                                                                        color: "black",
                                                                    }}
                                                                >
                                                                    : {event.place}
                                                                </div>
                                                            )}
                                                        </li>
                                                    );
                                                })}
                                        </ul>
                                    ) : (
                                        <p>  .</p>
                                    )
                                )}
                            </div>

                        )}

                        {activeTab === "" && (
                            <div className="participants-list">
                                <h3> </h3>
                                <ul>
                                    {teamMembers.length > 0 ? (
                                        teamMembers.map((member) => <li key={member._id}>{member.name}</li>)
                                    ) : (
                                        <li> .</li>
                                    )}
                                </ul>
                                {selectedTeamId && (
                                    <button
                                        onClick={() => generateInviteCode(selectedTeamId)}
                                        className="invite-button"
                                        style={{ backgroundColor: selectedTeamColor }}
                                    >
                                        +
                                    </button>
                                )}
                            </div>
                        )}
                    </div>

                    <div className="tab-button">
                        <button
                            className={activeTab === "" ? "active-tab" : ""}
                            onClick={() => setActiveTab("")}
                            style={{ backgroundColor: selectedTeamColor }}
                        >
                            {selectedTeamId ? "" : ""}
                        </button>
                        <button
                            className={activeTab === "" ? "active-tab" : ""}
                            onClick={() => setActiveTab("")}
                            style={{ backgroundColor: selectedTeamColor }}
                        >
                            
                        </button>
                    </div>
                </aside>

            </main>

            {/* Modal */}
            {isModalOpen && (
                <div className="modal-overlay" onClick={closeModal}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                        {!isJoinMode ? (
                            <>
                                <h2> </h2>
                                <input
                                    type="text"
                                    placeholder="  "
                                    value={teamName}
                                    onChange={(e) => setTeamName(e.target.value)}
                                />
                                {error && <p className="error-message">{error}</p>}
                                <button onClick={handleCreateTeam}> </button>
                                <button onClick={() => setJoinMode(true)}> </button>
                                <button className="cancel" onClick={closeModal}></button>
                            </>
                        ) : (
                            <>
                                <h2> </h2>
                                <input
                                    type="text"
                                    placeholder="  "
                                    value={inviteCode}
                                    onChange={(e) => setInviteCode(e.target.value)}
                                />
                                <button onClick={() => joinTeamWithCode(inviteCode, user._id)}></button>
                                <button onClick={() => setJoinMode(false)}></button>
                                <button className="cancel" onClick={closeModal}></button>
                            </>
                        )}
                    </div>
                </div>
            )}
        </div >
    );
}

export default Team;
